@use "../../assets/sass/variables" as *;

.showcase {
  display: none;
}

@media screen and (min-width: 560px) {
  .showcase {
    height: 100vh;
    width: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;

    &__background {
      height: 100%;
      width: 100%;
    }

    &__header {
      position: absolute;
      color: $color-white;
      font-size: 60px;
      text-align: center;
      width: 40%;
    }

    &__cover {
      height: 100%;
      width: 100%;
      position: absolute;
      background-color: $color-black;
      opacity: 0.7;
    }
  }
}
